import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'

import BookList from '@/components/book/list'
import BookCategoryList from '@/components/bookcategory/list'

import ProductList from '@/components/product/productList'

import UserList from '@/components/user/list'
import UserChangePwd from '@/components/user/changepwd'
import UserProfile from '@/components/user/profile'

import Index from '@/components/frontPage/index'
import Main from '@/components/frontPage/main'
import Product from '@/components/frontPage/product'
import About from '@/components/frontPage/about'

// 
const Login = resolve => require(['@/components/Login'], resolve)

Vue.use(Router)

let router = new Router({
// mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home2',
      title:'',
      component: Index,
      redirect: '/index',
      children:[
        {path:'index', title:'', component:Main}// ,falsetrue
      ]
    },
    {
      path: '/home',
      name: 'home',
      title:'',
      component: Index,
      redirect: '/index',
      children:[
        {path:'index', title:'', name:'home_index', component:Main}
      ]
    },
    {
      path: '/products',
      name: 'product',
      title:'',
      component: Index,
      children:[
        {path:'index', title:'', name:'product_name', component:Product}
      ]
    },
    {
      path: '/about',
      name: 'about',
      component: Index,
      children:[
        {path:'index', title:'', name:'about_index', component:About}
      ]
    },
    {
      path: '/login',
      name: '',
      component: Login
    },
    {
      path: '/manage',
      name: 'back_home',
      component: Home,
      redirect: '/dashboard',
      leaf: true, // 
      menuShow: true, // 
      iconCls: 'iconfont icon-home', // class
      children: [
        {path: '/dashboard', component: Dashboard, name: '', menuShow: true}
      ]
    },
    {
      path: '/manage',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/user/list', component: UserList, name: '', menuShow: true}
      ]
    },
    {
      path: '/manage',
      component: Home,
      name: '',
      menuShow: false, 
      iconCls: 'iconfont icon-books',
      children: [
        {path: '/book/list', component: BookList, name: '', menuShow: true},
        {path: '/book/category', component: BookCategoryList, name: '', menuShow: true}
      ]
    },
    {
      path: '/manage',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-books',
      children: [
        {path: '/product/productList', component: ProductList, name: '', menuShow: true},
        {path: '/book/category', component: BookCategoryList, name: '', menuShow: true}
      ]
    },
    {
      path: '/manage',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-setting1',
      children: [
        {path: '/user/profile', component: UserProfile, name: '', menuShow: true},
        {path: '/user/changepwd', component: UserChangePwd, name: '', menuShow: true}
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  //
  if (to.path.startsWith('/index') || to.path.startsWith('/home') || to.path.startsWith('/products') || to.path.startsWith('/about')) {
    next()
  } else {
    if (to.path.startsWith('/login')) {
      window.localStorage.removeItem('access-user')
      next()
    } else {
      let user = JSON.parse(window.localStorage.getItem('access-user'))
      // 
      if (!user) {
        next({path: '/login'})
      } else {
        next()
      }
    }
  }
})

export default router



